import orderStyles from "./orderDetails.module.css";
import DoneInPopup from "../../images/DoneInPopup.svg";
import { useSelector } from "react-redux";

const OrderDetails = () => {
  const isOrder = useSelector((state) => state.order.order); //   

  return (
    <div className={orderStyles.order__data}>
      <p className={`${orderStyles.order__number} text text_type_digits-large`}>{isOrder}</p>
      <p className={`${orderStyles.order__identifier} text text_type_main-medium mt-8`}> </p>
      <img className="mt-15 mb-15" src={DoneInPopup} alt=""></img>
      <p className="text text_type_main-small mb-2">   </p>
      <p className="text text_type_main-small text_color_inactive mb-20">    </p>
    </div>
  );
};

export default OrderDetails;
